import React, { useEffect, useRef } from 'react';
import { gsap } from 'gsap';
import 'splitting/dist/splitting.css';
import Splitting from 'splitting';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFilePdf, faArrowDown } from '@fortawesome/free-solid-svg-icons';

import Button from './Button';

const Hero = () => {
  const titleRef = useRef(null);
  const descriptionRef = useRef(null);
  const buttonsRef = useRef(null);
  const arrowRef = useRef(null);
  const heroRef = useRef(null);

  const animateIn = () => {
    let tl = gsap.timeline();
    tl.set(heroRef.current, { visibility: "visible"});
  }

  useEffect(() => {
    Splitting({ target: '.heading', by: 'chars' });
    Splitting({ target: '.subheading', by: 'lines' });
    animateIn();
  }, []);

  return (
    <div className="hero" ref={heroRef}>
      <h1>
        <span data-splitting="" className="heading" ref={titleRef}>Hello, I'm Arlo Adams.</span>
        <p data-splitting="" className="subheading" ref={descriptionRef}>
          I am a <b>Front-End Web Developer</b> creating interactive online experiences in Denver, Colorado.
        </p>
      </h1>
      <div className="buttons" ref={buttonsRef}>
        <Button
          text="My Work"
          link="#my-work"
        />
        <Button 
          classes="secondary"
          text={<><FontAwesomeIcon icon={faFilePdf} />&nbsp;&nbsp;Resume</>}
          link="ArloAdamsResume.pdf"
        />
      </div>
      <div className="scroll-arrow-container" ref={arrowRef}>
        <FontAwesomeIcon icon={faArrowDown} className="scroll-arrow"/>
      </div>
    </div>
  );
}

export default Hero;